}

.footeritem {
    display: flex;
    flex-direction: row;
    align-items: center; 
    margin: 5px;
    margin-right: 10px;
}

.footeritemtext {
    color: #40805d;
    white-space: nowrap;
    padding-left: 5px;
}

.dafooter {
    color: #40805d;
    background-color: #14281d;
    position:absolute;
    left:0;
    bottom:0;
    right:0;

}

